<template>
  <section class="welcome-video">
    <div class="container">
      <base-heading
        :title="getWelcomeVideo.title"
        level="2"
        customClass="welcome-video__title heading-h2"
      ></base-heading>

      <base-heading
        :title="getWelcomeVideo.subtitle"
        level="3"
        customClass="welcome-video__subtitle heading-h3"
      ></base-heading>

      <div class="video-container">
        <div
          class="video-placeholder"
          v-if="!isVideoVisible"
        >
          <img
            src="./../assets/img/video-thumbnail.png"
            alt="Video Thumbnail"
            class="video-thumbnail"
          >
          <button
            class="play-button"
            @click="toggleVideo"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="80"
              height="56"
              viewBox="0 0 80 56"
              fill="none"
            >
              <path
                d="M32 40L52.76 28L32 16V40ZM78.24 8.68C78.76 10.56 79.12 13.08 79.36 16.28C79.64 19.48 79.76 22.24 79.76 24.64L80 28C80 36.76 79.36 43.2 78.24 47.32C77.24 50.92 74.92 53.24 71.32 54.24C69.44 54.76 66 55.12 60.72 55.36C55.52 55.64 50.76 55.76 46.36 55.76L40 56C23.24 56 12.8 55.36 8.68 54.24C5.08 53.24 2.76 50.92 1.76 47.32C1.24 45.44 0.88 42.92 0.64 39.72C0.36 36.52 0.24 33.76 0.24 31.36L0 28C0 19.24 0.64 12.8 1.76 8.68C2.76 5.08 5.08 2.76 8.68 1.76C10.56 1.24 14 0.879999 19.28 0.639999C24.48 0.359999 29.24 0.24 33.64 0.24L40 0C56.76 0 67.2 0.64 71.32 1.76C74.92 2.76 77.24 5.08 78.24 8.68Z"
                fill="#FA4141"
              />
            </svg>
          </button>
        </div>

        <div
          id="youtube-video"
          class="youtube-video"
          v-if="isVideoVisible"
        >
          <iframe
            :src="videoSrc"
            title="Theory 1 | Designing the future of performance vehicles"
            frameborder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            referrerpolicy="strict-origin-when-cross-origin"
            allowfullscreen
            class="video"
          ></iframe>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex';
import BaseHeading from './base/BaseHeading.vue';

export default {
  data() {
    return {
      isVideoVisible: false,
      videoSrc: 'https://www.youtube.com/embed/FgOMdR_9ecY'
    }
  },
  components: {
    BaseHeading
  },
  computed: {
    ...mapGetters('home', ['getWelcomeVideo'])
  },
  mounted() {
    window.addEventListener('keydown', this.handleKeydown)
  },
  beforeUnmount() {
    window.removeEventListener('keydown', this.handleKeydown)
  },
  methods: {
    loadYouTubeAPI() {
      return new Promise((resolve, reject) => {
        if (window.YT) {
          // API уже загружен
          resolve(window.YT);
        } else {
          const script = document.createElement("script");
          script.src = "https://www.youtube.com/iframe_api";
          script.onload = () => resolve(window.YT);
          script.onerror = () => reject(new Error("Не удалось загрузить YouTube API"));
          document.head.appendChild(script);
        }
      })
    },
    toggleVideo() {
      this.isVideoVisible = !this.isVideoVisible
      if (this.isVideoVisible) {
        this.videoSrc += "?autoplay=1"
      } else {
        this.videoSrc = 'https://www.youtube.com/embed/FgOMdR_9ecY'
      }
    },
    handleKeydown(event) {
      console.log('play/stop');
      console.log(event);

      if (this.isVideoVisible) {
        switch (event.key) {
          case ' ':
          case 'Enter':
            event.preventDefault();
            this.toggleVideo()

            break;

          default:
            break;
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.welcome-video {
  padding-top: 150px;
}

.welcome-video__subtitle {
  margin-top: 20px;
}

.video-container {
  margin-top: 50px;
  max-width: 1000px;
  width: 100%;
  // height: 500px;
  overflow: hidden;
}

.video-placeholder {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.video-thumbnail {
  width: 100%;
}

.play-button {
  position: absolute;
  background-color: transparent;
  border: none;
  width: 80px;
  height: 56px;
  flex-shrink: 0;
}

.youtube-video {
  width: 1000px;
  height: 500px;
}

.video {
  width: 100%;
  height: 100%;
}
</style>